<template>
  <node :title="config.name" :show-error="showError" :content="content" :error-info="errorInfo"
    @selected="$emit('selected')" @delNode="$emit('delNode')" @insertNode="type => $emit('insertNode', type)"
    placeholder="" header-bgc="#f25643" header-icon="el-icon-time" />
</template>

<script>
import Node from './Node'

export default {
  name: "DelayNode",
  props: {
    config: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  components: { Node },
  data() {
    return {
      showError: false,
      errorInfo: '',
    }
  },
  mounted() {
    // this.config.props.placeholder=this.content;
  },
  watch: {
    content(v) {
      this.config.props.placeholder = v;
    }
  },
  computed: {
    content() {
      
      if (this.config.props.type === 'FIXED') {
        return ` ${this.config.props.time} ${this.getName(this.config.props.unit)}`
      } else if (this.config.props.type === 'AUTO') {
        return ` ${this.config.props.dateTime}`
      } else {
        return null
      }
    }
  },
  methods: {
    //
    validate(err) {
      this.showError = false
      try {
        if (this.config.props.type === "AUTO") {
          if ((this.config.props.dateTime || "") === "") {
            this.showError = true
            this.errorInfo = ""
          }
        } else {
          if (this.config.props.time <= 0) {
            this.showError = true
            this.errorInfo = ""
          }
        }
      } catch (e) {
        this.showError = true
        this.errorInfo = ""
      }
      if (this.showError) {
        err.push(`${this.config.name} `)
      }
      return !this.showError
    },
    getName(unit) {
      switch (unit) {
        case 'Y': return '';
        case 'M': return '';
        case 'D': return '';
        case 'TH': return '';
        case 'TM': return '';
        case 'TS': return '';
        default: return '';
      }
    }
  }
}
</script>

<style scoped></style>
